* {
    margin: 0;
    padding: 0;
    font-family: "Courier New", Courier, monospace;
}

.container {
    margin: auto;
    margin-top: 150px;
    width: 500px;
    box-sizing: border-box;
    padding: 30px;
    background-color: rebeccapurple;
}

.header {
    text-align: center;
    margin: 10px auto 30px;
    background-color: red;
    position: relative;
    left: 50%;
    transform: translateX(-50%);
    display: inline-block;
    border-radius: 20px;
    overflow: hidden;
}

.button {
    width: 70px;
    text-align: center;
    display: inline-block;
    padding: 10px 20px;
    user-select: none;
    cursor: pointer;
}

.inputs {
    width: 100%;
    padding: 10px;
    box-sizing: border-box;
    margin: 10px 0;
    border: none;
}

.inputs:focus {
    outline: 2px solid orange;
}